@import '~antd/dist/antd.css';

body {
    font-family: 'IBM Plex Sans';
}
.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.nav-container {
    position: sticky;
    top: 0;
    background-color: white;
    font-family: 'IBM Plex Sans';
    font-weight: 600;
    color: black;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-content: center;
}

.feedback-toggle:hover {
    cursor: pointer;
}

.nav {
    border-bottom: none;
    background: transparent;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 64px;
}

.doggy-icon {
    height: 300px;
}

.logo {
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.top-container {
    background-color: white;
    display: flex;
    justify-content: center;
}

.bottom-container {
    background-color: #0062ff;
    display: flex;
    justify-content: center;
    padding-bottom: 100px;
}
@media screen and (max-width: 768px) {
    .bottom-container {
        padding-bottom: 0px;
    }
}

.feedback-form-title {
    padding:  20px;
    color: #0062ff;
    font-weight: 600;
    font-size: 28px;
}

.feedback-header-container {
    padding:  20px;
    background-color: #0062ff;
    text-align: left;
    font-weight: 600;
    color: white;
}

.feedback-header-title {
    font-size: 24px;
    padding-bottom: 20px;
}

.feedback-header-description {
    font-size: 16px;
    font-family: 'Poppins';
    font-weight: 300;
}

.rating-container {
    padding:  20px;
    text-align: left;
    background-color: white;
    display: flex;
    flex-direction: column;
}

.rating-title {
    color: black;
    font-size: 20px;
    padding-bottom: 10px;
}

.rating {
    margin: 20px 0px;
}

.name-title {
    margin-top: 10px;
    padding-bottom: 5px;
    font-size: 16px;
}

.title-error {
    margin-top: 10px;
    padding-bottom: 5px;
    font-size: 16px;
    color: #da1e28;
    font-weight: 600;
}

.name-title-active {
    margin-top: 10px;
    font-weight: 600;
    color: #054ada;
    padding-bottom: 5px;
    font-size: 16px;
}

.name-input {
    padding: 10px 20px;
    background-color: #C9deff;
    color: #4c4c4c;
    border: none;
    font-family: 'Poppins';
    font-weight: 300;
}
.name-input:focus {
    color: black;
    outline: none;
    font-weight: 400;
}

.experience-title {
    margin-top: 10px;
    padding-bottom: 5px;
    font-size: 16px;
}

.experience-title-active {
    margin-top: 10px;
    font-weight: 600;
    color: #054ada;
    padding-bottom: 5px;
    font-size: 16px;
}

textarea {
   resize: none;
   border: none;
   background-color: #C9deff;
   color: #4c4c4c;
   padding: 10px 20px;
   height: 300px;
   font-family: 'Poppins';
   font-weight: 300;
}
textarea:focus {
    color: black;
    outline: none;
    font-weight: 400;
}

.submit-button {
    border: none;
    background-color: #0062ff;
    color: white;
    margin-top: 20px;
    padding: 20px;
    font-weight: 600;
    font-size: 20px;
}
.submit-button:hover {
    cursor: pointer;
}

.submit-button:active {
    background-color: #054ada;
}

.error-text {
    color: #da1e28;
    font-weight: 400;
    font-family: 'Poppins';
    font-size: 14px;
    margin-left: 10px;
}

.feedback-page {
    display: flex;
    flex-direction: column;
    padding: 40px;
}

.feedback-page-title {
    color: #054ada;
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 40px;
    position: sticky;
}

.feedback-container {
    width: fit-content;
    margin-bottom: 20px;
}

.card-name {
    font-size: 24px;
    font-weight: 600;
    color: #054ada;
    padding-left: 5px;
}

.card-container {
    font-family: 'Poppins';
    font-size: 20px;
    font-weight: 300;
    background-color: #0062ff;
    padding: 20px;
    margin-bottom: 5px;
    color: white;
    border-radius: 10px;
}

.card-rating {

}